import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import Bmob from '@/utils/bmob.js';
import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';

let that;
let search_text;
let uid;
export default {
	components: {
		faIcon,
		uniSegmentedControl
	},
	data() {
		return {
			items: [
				'销售客户',
				'供货商',
			],
			current: 0,
			people: null, //获得人员数组
		}
	},
	onLoad() {
		that = this;
		uid = uni.getStorageSync('uid');
	},

	onShow() {
		uni.removeStorageSync("customs")
		uni.removeStorageSync("custom_type")
		if (that.current == 0) {
			that.load_data("customs")
		} else {
			that.load_data("producers")
		}

	},
	methods: {

		//编辑操作
		edit(item) {
			uni.setStorageSync("customs", item);
			if (that.current == 0) {
				uni.setStorageSync("custom_type", "customs");
			} else {
				uni.setStorageSync("custom_type", "producers");
			}
			uni.navigateTo({
				url: "add/add"
			})
		},

		//删除操作
		delete_this(id) {
			uni.showModal({
				title: '提示',
				content: '是否删除此客户',
				success: function(res) {
					if (res.confirm) {
						console.log(id);
						if (that.current == 0) {
							that.delete_data("customs", id)
						} else {
							that.delete_data("producers", id)
						}
					}
				}
			});
		},

		//删除数据
		delete_data(type, id) {
			console.log(id)
			const query = Bmob.Query(type);
			query.destroy(id).then(res => {
				console.log(res)
				uni.showToast({
					title: "删除成功",
					icon: "none"
				})
				that.load_data(type)
			}).catch(err => {
				console.log(err)
			})
		},





































		//tab点击
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index

				if (this.current == 0) {
					that.load_data("customs")
				} else {
					that.load_data("producers")
				}
			}
		},

		//加载数据
		load_data(type) {
			uni.showLoading({
				title: "加载中..."
			})
			const query = Bmob.Query(type);
			query.equalTo("parent", "==", uid);
			query.limit(500);
			if (search_text) {
				if (type == "customs") {
					query.equalTo("custom_name", "==", {
						"$regex": "" + search_text + ".*"
					});
				} else {
					query.equalTo("producer_name", "==", {
						"$regex": "" + search_text + ".*"
					});
				}

			}
			query.find().then(res => {
				console.log(res)
				uni.hideLoading();
				that.people = res;
			});
		},

	}
}
